'use client'
import React, { useState } from "react";
import { Editor, EditorState, RichUtils } from "draft-js";
import "draft-js/dist/Draft.css"; // Import Draft.js styles

const AddProduct = () => {
  const [editorState, setEditorState] = useState(EditorState.createEmpty());

  const handleEditorChange = (state) => {
    setEditorState(state);
  };

  const handleBoldClick = () => {
    setEditorState(RichUtils.toggleInlineStyle(editorState, "BOLD"));
  };

  const handleItalicClick = () => {
    setEditorState(RichUtils.toggleInlineStyle(editorState, "ITALIC"));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // Convert editor state to raw content for storage or submission
    const content = editorState.getCurrentContent();
    console.log("Submitted Content:", content.getPlainText());
  };

  return (
    <div className="bg-gray-100 min-h-screen flex items-center justify-center">
      <div className="bg-white w-full max-w-4xl p-8 rounded-lg shadow-lg">
        <h1 className="text-2xl font-semibold mb-6">Add New Product</h1>

        <form className="space-y-6" onSubmit={handleSubmit}>
          {/* Product Name */}
          <div>
            <label className="block text-sm font-medium text-gray-700">Product Name</label>
            <input
              type="text"
              placeholder="Enter product name"
              className="mt-1 w-full p-2 border border-gray-300 rounded-md"
            />
          </div>

          {/* Product Description with Draft.js */}
          <div>
            <label className="block text-sm font-medium text-gray-700">Description</label>
            <div className="mt-1 border border-gray-300 rounded-md p-2 bg-white">
              <div className="mb-2">
                <button
                  type="button"
                  onClick={handleBoldClick}
                  className="px-3 py-1 bg-gray-200 text-gray-800 rounded-md mr-2 hover:bg-gray-300"
                >
                  Bold
                </button>
                <button
                  type="button"
                  onClick={handleItalicClick}
                  className="px-3 py-1 bg-gray-200 text-gray-800 rounded-md hover:bg-gray-300"
                >
                  Italic
                </button>
              </div>
              <div className="p-2 border border-gray-300 rounded-md">
                <Editor
                  editorState={editorState}
                  onChange={handleEditorChange}
                  placeholder="Write the product description here..."
                />
              </div>
            </div>
          </div>

          {/* Other Fields */}
          <div>
            <label className="block text-sm font-medium text-gray-700">Category</label>
            <select className="mt-1 w-full p-2 border border-gray-300 rounded-md">
              <option>Select Category</option>
              <option>Electronics</option>
              <option>Clothing</option>
              <option>Home Appliances</option>
            </select>
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700">Price</label>
            <input
              type="number"
              placeholder="Enter price"
              className="mt-1 w-full p-2 border border-gray-300 rounded-md"
            />
          </div>

          {/* Submit Button */}
          <div>
            <button
              type="submit"
              className="w-full bg-blue-500 text-white py-2 px-4 rounded-md hover:bg-blue-600 transition"
            >
              Submit Product
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default AddProduct;
